




































































































import {defineComponent, ref, watch, reactive} from '@vue/composition-api';
import Game from "./Game.vue"


export default defineComponent({
  components: {
    Game
  },
  setup(props, ctx) {
    const counter = ref(0);
    const vuur = ref<HTMLVideoElement>(null);

    document.addEventListener("game-finished", function () {
      next()
    })

    document.addEventListener('keyup', event => {
      if (event.code === 'Space') next()
    })

    function next(){
      counter.value++;
      if (counter.value == 3) document.dispatchEvent(new Event("play-music"))
      const a = document.createElement('a');
      a.setAttribute('href', "#section" + counter.value);
      a.click();
    }

    watch(vuur, (newValue, oldValue) => {
      vuur.value.onended = function () {
        const audio = document.getElementById("age") as HTMLAudioElement;
        const link = document.getElementById("link") as HTMLHRElement;
        link.click()
        audio.play();
        setTimeout(pageScroll, 500)
      }
    })

    function pageScroll() {
      window.scrollBy(0,20);
      if (document.documentElement.scrollTop <= 16500) setTimeout(pageScroll,10);
      else next();
    }

    return { vuur, counter}
  }
})

