import {Scene} from 'phaser'
import logo from "../assets/logo.png"

let player;
let cursors;
let scream;
let caras;
let sound;
let bgMusic;

export default class MainScene extends Scene {
    constructor() {
        super({key: 'MainScene.js'})
    }

    preload() {
        this.load.tilemapTiledJSON('map', 'assets/map.json');
        this.load.spritesheet('tiles', 'assets/tiles.png', {frameWidth: 70, frameHeight: 70});
        this.load.spritesheet('player', 'assets/player.png', {frameWidth: 35, frameHeight: 60});
        this.load.image("cara", "assets/cara.png")
        this.load.audio('scream', ["assets/scream.mp3"]);
        this.load.audio('blink', ["assets/blink.mp3"]);
    }

    create() {
        cursors = this.input.keyboard.createCursorKeys();
        player = this.physics.add.sprite(0, 370, 'player', 0);
        scream = this.sound.add('scream');
        sound = this.sound;

        document.addEventListener("play-music", this.test)

        for (let i = 1; i < 9; i++) {
            this.createCara(i * 250, 430)
        }

        for (let i = 1; i < 4; i++) {
            this.createCara(i * 100 + 300, 220)
        }

        for (let i = 1; i < 4; i++) {
            this.createCara(i * 100 + 1000, 220)
        }

        for (let i = 1; i < 3; i++) {
            this.createCara(i * 100 + 1500, 220)
        }

        this.createCara(500, 50)
        this.createCara(800, 50)

        this.createCara(1000, 10)


        this.createCara(1200, 20)
        this.createCara(1400, 10)
        this.createCara(1600, 100)

        caras = 22;


        this.anims.create({
            key: "walk",
            frames: this.anims.generateFrameNumbers('player', {start: 0, end: 3}),
            frameRate: 8,
            repeat: -1
        });

        this.anims.create({
            key: 'idle',
            frames: this.anims.generateFrameNames('player', {start: 0, end: 0}),
            frameRate: 8,
            repeat: -1
        });

        player.setBounce(0.2); // our player will bounce from items
        player.setCollideWorldBounds(true); // don't go out of the map
        player.setScale(2, 2)

        const map = this.make.tilemap({key: 'map'});

        // tiles for the ground layer
        var groundTiles = map.addTilesetImage('tiles');
        // create the ground layer
        const groundLayer = map.createDynamicLayer('World', groundTiles, 0, 0);
        // the player will collide with this layer
        groundLayer.setCollisionByExclusion([-1]);

        // set the boundaries of our game world
        this.physics.world.bounds.width = groundLayer.width;
        this.physics.world.bounds.height = groundLayer.height;

        this.physics.add.collider(groundLayer, player);

        this.cameras.main.setBounds(0, 0, map.widthInPixels, map.heightInPixels);
        this.cameras.main.startFollow(player);
        this.cameras.main.followOffset.set(0, 75);
        this.cameras.main.setBackgroundColor('#87ceeb');
        this.cameras.main.setZoom(2.5);
    }

    update() {
        if (cursors.left.isDown) {
            player.setVelocityX(-300)
            player.play('walk', true); // play walk animation
            player.flipX = true; // flip the sprite to the left
        } else if (cursors.right.isDown) {
            player.setVelocityX(300);
            player.play('walk', true); // play walk animation
            player.flipX = false; // flip the sprite to the left
        } else {
            player.body.setVelocityX(0);
            player.play('idle', true);
        }

        if (cursors.up.isDown && player.body.onFloor()) {
            player.setVelocityY(-400)
        }
    }

    onTrigger(player, cara) {
        scream.play();
        cara.destroy();
        caras--;
        if (caras === 0) {
            document.dispatchEvent(new Event("game-finished"))
            bgMusic.pause();
            this.game.destroy(true)
        }
    }


    createCara(x, y) {
        const cara = this.physics.add.sprite(x, y, "cara");
        cara.body.allowGravity = false;
        cara.setScale(0.7, 0.7);
        this.physics.add.overlap(player, cara, this.onTrigger);
    }

    test() {
        bgMusic = sound.add("blink");
        bgMusic.play();
    }
}



